.container {
    min-height: 100%;
    display: flex;
    flex-direction: row;
}

.latestDocuments {
    width: 280px;
    padding: 1rem;
    border-left: 1px solid #D9D9D9;
}

.main {
    min-width: 500px;
    padding: 1rem;
    flex: 1;
}

.boardHeader {
    flex-wrap: nowrap;
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    padding: 0 0.5rem 0 0.5rem;
    height: 30px;
}

.headerName {
    white-space: nowrap;
    font-weight: 700;
}

.seeAll {
    font-weight: 300;
    font-size: 14px;
    color: #aaaaaa;
    border-bottom: 1.5px solid rgba(186, 186, 186, 0.51);
    cursor: pointer;
    transition: 0.1s;
}

.seeAll:hover {
    border-color: rgba(186, 186, 186, 1);
}

.createNew {
    margin-left: 1rem;
    width: 150px;
}

.createNewButton {
    font-weight: 400;
    font-size: 16px;
    height: 30px;
}

.boardItem {
    user-select: none;
    cursor: pointer;
    position: relative;
    height: 150px;
    margin: 0.5rem;
    padding: 1rem 3rem 1rem 1rem;
    border: 1px solid #D9D9D9;
    border-radius: 0.15rem;
    background-color: #F0F4F7;
    transition: ease-in-out 0.1s;
}

.boardItemDate {
    font-size: 0.8rem;
}

.boardItemText {
    font-size: 1.25rem;
}

.boardItemIcon {
    position: absolute;
    right: 10px;
    bottom: 15px;
}

.listHeader {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    height: 30px;
}

.listItem {
    position: relative;
    user-select: none;
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 80px;
    border: 1px solid #D9D9D9;
    border-radius: 0.15rem;
    background-color: #F0F4F7;
    transition: ease-in-out 0.1s;
    margin-bottom: 1rem;
}

.listItemText {
    font-size: 0.9rem;
    font-weight: 400;
}

.listItemIconContainer {
    display: flex;
    height: 100%;
    padding-top: 0.7rem;
    justify-content: center;
}

.boardItem.pressing, .listItem.pressing {
    opacity: 0.2;
}